export class Site {
	constructor(selector) {
		this.$el = document.querySelector(selector)
	}

	render(model) {
		//—á–∏—Å—Ç–∏—Ç —Ñ–æ—Ä–º—É
		this.$el.innerHTML = ''
		model.forEach(block => {
			// —ç—Ç–æ—Ç –º–µ—Ç–æ–¥ –ø–æ–∑–≤–æ–ª—è–µ—Ç –≤—Å—Ç–∞–≤–ª—è—Ç—å –≤ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–Ω–æ–µ –º–µ—Å—Ç–æ HTML (beforeend - —Å–º. –≤ –¥–æ–∫—É–º–µ–Ω—Ç–∞—Ü–∏–∏)
			this.$el.insertAdjacentHTML('beforeend', block.toHTML()) 
		
		})
	}
}